.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    height: 100%;
    min-height: 400px;
}

.project-card img {
    width: 100%;
}

.project-title{
    color: #fff;
    padding: 1rem 0;
    
}

.pro-details {
    padding-bottom: 1rem;
    font-size: 0.8rem;
    text-align: justify;
    flex-grow: 1;
    margin-bottom: 3rem;
}

.pro-details p{
    font-family: "Montserrat", sans-serif;
}

.pro-btns{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.5rem 0;
}

.pro-btns .btn{
    position: absolute;
    bottom: 20px;
    padding: 0.5rem 3.5rem;
    width: 50%;
    text-align: center;
}

@media screen and (max-width:740px){
    .project-container{
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
}